main {
  display: flex;
  flex-direction: column;
  flex: 1;

  section {
    flex: 1;

    article {
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin: 1em;
      flex: 1;
    }
  }

  h1,
  h2 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 1em;
  }

  h3 {
    font-size: 1.3rem;
    font-weight: 600;
    margin-bottom: 1em;
  }

  p {
    margin-bottom: 1em;

    &:last-child {
      margin-bottom: 0;
    }
  }

  pre {
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    max-width: calc(100vw - 2em);
    margin-bottom: 1em;
    padding: 1em;
    background-color: var(--theme-bg);
    will-change: auto;
    transition: var(--theme-transition);

    &:hover {
      .content__clip {
        opacity: 1;
      }
    }
  }

  code {
    --scroll-dimension: 0.25em;
    --scroll-border-radius: 1em;
    --scroll-color-thumb: rgb(0 0 0 / 50%);
    --scroll-color-track: rgb(0 0 0 / 20%);
    --scroll-background: transparent;

    display: flex;
    font-size: inherit;
    word-wrap: normal;
    color: var(--theme-text);
    overflow-x: auto;
    padding-block: 1em;
    -ms-overflow-style: inherit;
    scrollbar-width: thin;
    scrollbar-color: var(--scroll-color-thumb) var(--scroll-color-track);

    &::-webkit-scrollbar {
      display: block;
      width: var(--scroll-dimension);
      height: var(--scroll-dimension);
    }

    &::-webkit-scrollbar-thumb {
      background-color: var(--scroll-color-thumb);
      border-radius: var(--scroll-border-radius);
    }

    &::-webkit-scrollbar-track {
      background-color: var(--scroll-color-track);
      border-radius: var(--scroll-border-radius);
      border: var(--scroll-dimension) solid var(--scroll-background);
    }
  }

  kbd {
    display: inline-block;
    min-width: 2em;
    text-align: center;
    border: 1px solid var(--theme-text);
    border-radius: 0.25em;
    margin-block: 0.25rem;
    padding: 0.25em 0.5em;
  }

  ul {
    list-style: none;
    padding: 0;
  }

  li {
    margin-bottom: 0.5em;

    &:last-child {
      margin-bottom: 0;
    }
  }

  a {
    color: var(--theme-accent);
    text-decoration: none;
    cursor: pointer;
    will-change: auto;
    transition: var(--theme-transition);

    &:hover {
      text-decoration: underline;
    }

    // Focus
    &:focus,
    :focus-visible {
      outline: 1px solid var(--theme-accent);
      outline-offset: 5px;
    }
  }
}

.content {
  &__item {
    display: flex;
    justify-content: center;

    &:nth-child(even) {
      background-color: var(--theme-bg-alt);
    }
  }
}

// Breakpoint
@include media-breakpoint-up(lg) {
  main {
    section {
      article {
        margin: 1em auto;
        @include media-container(xl);
      }
    }
  }
}