header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem 1em;
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: var(--theme-accent);

  nav {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 1em;
    min-width: 143px;
  }

  a {
    color: var(--theme-text);
    text-decoration: none;
    cursor: pointer;
    min-width: 35px;
    will-change: auto;
    transition: var(--theme-transition);

    &:hover {
      text-decoration: underline;
    }

    // Focus
    &:focus,
    :focus-visible {
      outline: 1px solid var(--theme-text);
      outline-offset: 5px;
    }
  }
}

.logo {
  --logo-width: 65px;

  display: block;
  width: var(--logo-width);

  img {
    height: auto;
  }
}

// Breakpoint
@include media-breakpoint-up(md) {
  .logo {
    --logo-width: 80px;
  }
}
